import { useNavigate } from 'react-router-dom';
import { useConnectionStore } from '../stores/connectionStore';
import { useDeviceStore } from '../stores/deviceStore';
import { ChevronRight, Radio, Wifi, Home, Users, Info, Zap, BarChart3 } from 'lucide-react';

function SettingsItem({ icon: Icon, label, value, onClick, valueColor }: {
  icon: React.ElementType; label: string; value?: string; onClick?: () => void; valueColor?: string;
}) {
  return (
    <div
      onClick={onClick}
      className="flex items-center justify-between px-4 py-3.5 transition-colors"
      style={{ cursor: onClick ? 'pointer' : 'default' }}
    >
      <div className="flex items-center gap-3">
        <Icon size={20} style={{ color: 'var(--text-tertiary)' }} />
        <span className="text-[15px]">{label}</span>
      </div>
      <div className="flex items-center gap-1 text-[14px]" style={{ color: valueColor || 'var(--text-tertiary)' }}>
        {value && <span>{value}</span>}
        {onClick && <ChevronRight size={16} />}
      </div>
    </div>
  );
}

function SettingsSection({ title, children }: { title: string; children: React.ReactNode }) {
  return (
    <div className="mb-6 px-5">
      <div className="mb-2 text-[12px] font-semibold uppercase tracking-wider" style={{ color: 'var(--text-tertiary)' }}>
        {title}
      </div>
      <div className="overflow-hidden rounded-xl border" style={{ background: 'var(--bg-card)', borderColor: 'var(--z2m-border)' }}>
        {children}
      </div>
    </div>
  );
}

export function Settings() {
  const navigate = useNavigate();
  const connectionState = useConnectionStore((s) => s.state);
  const allIds = useDeviceStore((s) => s.allIds);
  const isConnected = connectionState === 'connected';

  return (
    <div>
      <div className="px-5 pb-4 pt-2">
        <h1 className="text-[24px] font-bold">設定</h1>
      </div>

      <SettingsSection title="連線">
        <SettingsItem
          icon={Radio}
          label="Zigbee 網路"
          value={isConnected ? '● 正常' : '● 離線'}
          valueColor={isConnected ? 'var(--z2m-green)' : 'var(--z2m-red)'}
        />
        <div style={{ borderTop: '1px solid var(--z2m-border)' }} />
        <SettingsItem
          icon={Wifi}
          label="MQTT 狀態"
          value={isConnected ? '● 已連線' : '● 未連線'}
          valueColor={isConnected ? 'var(--z2m-green)' : 'var(--z2m-red)'}
        />
      </SettingsSection>

      <SettingsSection title="管理">
        <SettingsItem icon={Home} label="房間管理" onClick={() => navigate('/rooms')} />
        <div style={{ borderTop: '1px solid var(--z2m-border)' }} />
        <SettingsItem icon={Users} label="群組管理" onClick={() => navigate('/groups')} />
      </SettingsSection>

      <SettingsSection title="關於">
        <SettingsItem icon={Info} label="Z2M Lite 版本" value="1.0.0" />
        <div style={{ borderTop: '1px solid var(--z2m-border)' }} />
        <SettingsItem icon={Zap} label="Zigbee2MQTT" value="已連線" />
        <div style={{ borderTop: '1px solid var(--z2m-border)' }} />
        <SettingsItem icon={BarChart3} label="設備總數" value={`${allIds.length} 個`} />
      </SettingsSection>
    </div>
  );
}
